  background-image: url("/static/media/DomeWordSingle GOLD-ffcd00.ada69b34.png");
}

.acknowledge {
  padding-left: 60px;
  font-family: Montserrat, sans-serif;
  color: #fff;
  text-align: left;
}

.nsf-logo {
  display: inline-block;
  width: 100%;
  height: 100%;
  background-size: contain;
